/**
 * loading 加载动画
 * @description 此组件为一个小动画。
 * @data {String} theme 主题色,仅有light/dark
 * @data {Boolean} show 是否显示动画
 * @data {String} title loading文字
 * @data {String} type 模式选择默认loading加载中(可选circle) success成功 warning警告
 * @data {String} color 动画活动区域的颜色，只对 type = circle 模式有效（默认#c7c7c7）
 * @data {String Number} size 加载图标的大小，单位px
 */
export default {
  name: "vLoading",
  data() {
    return {
      theme: "light",
      show: false,
      title: "",
      type: "loading",
      color: "#c7c7c7",
      size: "50"
    };
  },
  computed: {
    // 加载中圈动画的样式
    cricleStyle() {
      let style = {};
      if (this.type == "circle") {
        style.width = parseInt(this.size * 0.8) + "px";
        style.height = parseInt(this.size * 0.8) + "px";
        style.borderColor = `#e4e4e4 #e4e4e4 #e4e4e4 ${
          this.color ? this.color : "#c7c7c7"
        }`;
      } else if (this.type == "warning") {
        style.width = parseInt(this.size * 0.8) + "px";
        style.height = parseInt(this.size * 0.8) + "px";
      } else {
        style.width = this.size + "px";
        style.height = this.size + "px";
      }
      return style;
    }
  },
  methods: {
    showLoading() {
      this.show = true;
    },
    hideLoading() {
      this.show = false;
    },
    // 重置data对象到初始化状态
    setDefaultOptions() {
      Object.assign(this.$data, this.$options.data()); 
    }
  }
};



